import React from 'react';
import my_works from '../my_works';

let itog = [];

export default class Tag extends React.Component {
    state = {
        tags: [ 'wordpress', 'html', 'jquery', 'php', 'react', 'bootstrap', 'javascript'],
        tag_length: ''
    }
    filter = (tag) => {
        this.props.filter(tag)
    }
    tag_length = (tag) => {
        let mass = [];

        let cnt = 0;
        for(let i of my_works) {
            i.tags.map(item => mass.push(item))
        }
        for(let i of mass) {
            if(i === tag) {
                cnt++
            }
        }
        itog.push(cnt);

    }
    render() {
        return (
            this.state.tags.map((tag, i) => {
                let cnt = 0;
                for(let i of my_works) {
                    i.tags.map(item => {
                        if(item === tag)
                        cnt++
                    });
                }
                itog.push(cnt);
                return <div className="tag_wrap"><img src={`img/icons/${tag}.png`} alt="icon" className={`icon`} key={tag} onClick={() => this.filter({tag})}></img><div className="cnt">{itog[i]}</div></div>
            })

        )
    }
}



