import React from 'react';
import ReactDOM from 'react-dom';
import 'materialize-css/dist/css/materialize.min.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import './index.css';
import my_works from './my_works'

import Header from './components/Header';
import Footer from './components/Footer';
import Card from './components/Card';
import Tag from './components/tag';

class Render extends React.Component {
    state = {
        my_works: my_works.reverse()
    }
    filter = (arg) => {
        let mass_filt = my_works.filter((i) => {
            return i.tags.some(elem => elem === arg.tag)
        })
        mass_filt.map((item) => {
            return item.id +=10
        })
        this.setState({
            my_works: mass_filt
        })
    }
    showAll = () => {
        this.setState({
            my_works: my_works
        })
    }
    render() {
        return (
            <div>
                <Header />
                <main>
                <div className="wrapper">
                    <div className="main-wrap">
                            <ReactCSSTransitionGroup className="cards" transitionName="change" transitionEnter={true} transitionLeave={false} transitionEnterTimeout={500}>
                            {this.state.my_works.map((item, id) => {
                                return <Card item={item} key={item.id} id={item.id} img={item.img} title={item.title} text={item.text} link={item.link} tags={item.tags} />
                            })}
                            </ReactCSSTransitionGroup>
                        <div className="tags">
                            <a className="icon icon_all" onClick={this.showAll}>All</a>
                            <Tag filter={this.filter}/>
                        </div>
                    </div>
                </div>
                </main>
                <Footer />
            </div>
        )
    }
}

ReactDOM.render(<Render />, document.getElementById('root'));



